    <p v-show="!login">
      <router-link to="/dl">登录</router-link>|<a href="#">注册</a>
    </p>
    <router-link v-show="login" to="/user"><van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" /></router-link>
  </div>
</van-sticky>
<!-- 内容部分 -->
<div class="content">
<!-- 提示语言 -->
<div class="top">
<span>当前定位城市：</span>
<span style="font-weight:900">定位不准时，请在城市列表中选择</span>
</div>
<div class="top-bj">
<span @click="tiao(3)">北京</span>
      <span>></span>
</div>
<!-- 热门 -->
<div class="hot">
  <p>热门城市</p>
  <ul>
    <li :key="index" v-for="(item,index) in hot">
     <span @click="tiao(item.id)">
        {{ item.name }}
      </span>
      </li>
  </ul>
</div>
