<!-- 结合版 -->
<van-index-bar :index-list="dc" :sticky-offset-top="46">
  <div class="word" :id="index" :key="index" v-for="(item,index) in dc">
  <div><van-index-anchor :index="item" /><p v-if="index=='A'">(按字母排序)</p></div> 
  <ul>
    <li :key="xb" v-for="(nr,xb) in city[item]">
      <span @click="tiao(nr.id)">
        {{ nr.name }}
      </span>
