<script>
// import func from '../../vue-temp/vue-editor-bridge'
import {ajaxCity} from '../untils/api'
export default {
 // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
       hot: [],
       city:{},
       dc:[],
       login:false,

   }
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    // 跳转到详情页面
    async  tiao(i){
      //  console.log(i)
      let {data} = await this.$http.get(`http://elm.cangdu.org/v1/cities/${i}`)
      // console.log(data)
      sessionStorage.setItem('city',JSON.stringify(data)) 
      this.$router.push({
        path:'/city'
      })
     }
  },
  // 切换登录状态
  created(){
    let a= sessionStorage.getItem('token')
    if(a){
      this.login=true
    }else{
      this.login=false
    }
  },
  // 接受数据
  async mounted(){

    // 获取hot数据
    let {data:hot} = await ajaxCity("hot")
    this.hot = hot
    let {data:city} =await ajaxCity("group")
    
    // 英文排序
    //  Object.keys(city)
    //     .sort()
    //     .map((key) => {
    //       this.city[key] = city[key];
    //     });
    //      console.log(this.city)
    //      this.$set(this.city)
    this.city = city
    this.dc = Object.keys(city).sort()
    // console.log(city,hot)
  }
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss" scoped>
