  flex-wrap: wrap;
}
.word li{
  width: 90px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.word a{
  color:black;
}
.word div{
  display: flex;
  border-bottom: #ccc 1px solid;
   box-sizing: border-box;
   align-items: center;
   height: 40px;
   padding: 10px;
}
.word span{
  padding-right: 10px;
}
/* 导航 */
.box>ul{
  position:fixed;
  top:0;
}
.content{
  flex:1;
  // overflow: auto;
} 
