<template>
  <div class="box">
    <!-- 顶部 -->
    <van-sticky>
      <div>
        <router-link to="/pb">〈</router-link>
        <h2>密码登录</h2>
      </div>
    </van-sticky>
    <!-- 输入部分 -->
    <ul>
      <li><input type="text" placeholder="账号" v-model="arr.phone" @blur="shi" /></li>
      <li>
        <input
          :type="flag ? 'password' : 'text'"
          v-model="arr.mm"
          placeholder="密码"
        /><Mm :flag="flag" @dian="dian" />
      </li>
      <li>
        <input type="text" placeholder="验证码" v-model="arr.yz" /><img
          :src="img"
          alt=""
        />
        <div>
          <span>看不清</span
          ><span style="color: #3190e8" @click="yzm">换一张</span>
        </div>
      </li>
    </ul>
    <p>温馨提示：未注册过的账号，登录时将自动注册</p>
    <p>注册过的用户可凭账号密码登录</p>
    <button @click="join" :disabled="!can">登录</button>
  </div>
</template>

<script>
import Mm from "../components/Mm";
// 关于跨域接口
import axios from 'axios'
axios.defaults.withCredentials = true; //配置为true
export default {
  // 组件参数 接收来自父组件的数据
  props: {},

  // 组件状态值
  data() {
    return {
      img: "",
      arr: { phone: "", mm: "", yz: "" },
      flag: false,
      can: false,
    //   zz:
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {
    arr: {
      handler(val) {
        if (val.phone != "" && val.mm != "" && val.yz != "") {
          this.can = true;
        } else {
          this.can = false;
        }
      },
      deep: true,
    },
  },
  // 组件方法
  methods: {
    // 登录的事件，也不知道行不行
     setCookie(key,value,t){
            var oDate=new Date();
            oDate.setDate(oDate.getDate()+t);
            document.cookie=key+"="+value+"; expires="+oDate.toDateString();
        },
    //   渲染验证码
    async yzm() {
      // 获取图片数据
      let { data } = await this.$site.post("/v1/captchas");
      this.img = data.code;
      console.log(this.img);
    },

    //   登录事件
    async join() {
      // 这里是老师方法自带的，还不太理解
            this.setCookie("cap",this.arr.yz,2)
            this.setCookie("SID",100,2)
          // 这里也是，尴尬~
            let { data:res } = await axios.post("/api/v2/login",{
                username:this.arr.phone,
                password:this.arr.mm,
                captcha_code:this.arr.yz
            })
            // 验证并跳转页面，把id设为token值，使PB页面发生变化
            if(res.message!='登陆失败'){
              console.log(111)
              sessionStorage.setItem('token',JSON.stringify(res.user_id))
              this.$router.push('/pb')
            }
            
    },
    // 点击密码切换
    dian() {
      this.flag = !this.flag;
    },
    // 账号验证
    shi(){
         if(!(/^1[3456789]\d{9}$/.test(this.arr.phone))){ 
        alert("手机号码有误，请重填");
        this.arr.phone=''
        return false; 
        
    } 
    },
    
  },
  mounted() {
    // 调用验证码
    this.yzm();
  },
  components: {
    Mm,
  },
};
</script>

<style lang="scss" scoped>
// 顶部
.van-sticky > div {
  width: 100%;
  height: 44px;
  background: #3190e8;
  color: white;
  display: flex;
  align-items: center;
  font-size: 16px;
  box-sizing: border-box;
  padding: 10px;
  h2 {
    margin-left: 36%;
  }
  a {
    color: white;
  }
}

// 每一项输入框
ul {
  margin-top: 20px;
}
li {
  width: 100%;
  height: 50px;
  background: white;
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0 20px;
  input {
    border: none;
  }
  div {
    width: 60px;
  }
}
// 提示语
.box > p {
  color: red;
  font-size: 14px;
  display: flex;
  margin-left: 20px;
}
// 按钮
button {
  width: 90%;
  background: #4cd964;
  color: white;
  font-size: 20px;
  height: 50px;
  border: none;
  border-radius: 5px;
}
</style>
