<template>
<div class="box" @click="dian" :class="flag?'active':''">
    <span></span>
    <p>abc...</p>
</div>
</template>

<script>
export default {
  // 组件名称
  name: 'Mm',
  // 组件参数 接收来自父组件的数据
  props: ["flag"],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {}
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
      dian(){
          this.$emit('dian')
      }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss" scoped>
.box{
    position: relative;
}
span{
    display: block;
    width: 30px;
    height: 30px;
    background: #eee;
    border-radius: 20px;
    position: absolute;
    top: 10px;
}
p{
    width: 60px;
    height: 18px;
    background: #ccc;
    color: white;
    border-radius: 10px;
}
.active{
span{
   right:0;
}
p{
    background: #4cd964;
}
}
</style>
