<template>
<div class="box">
     <!-- 顶部 -->
    <van-sticky>
      <div>
       <van-icon name="search" />
        <p>{{site}}</p>
        <van-icon @click="tiao" name="contact" />
      </div>
    </van-sticky>
    <!-- 九宫格 -->
    <div class="center">
        <van-swipe @change="onChange">
        <van-swipe-item>
            <ul>
                <li :key="index" v-for="(item,index) in nav" v-show="index<8">
                    <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="">
                    <p>{{item.title}}</p>
                </li>
            </ul>
        </van-swipe-item>
        <van-swipe-item>
<ul>
                <li :key="index" v-for="(item,index) in nav" v-show="index>=8">
                    <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="">
                    <p>{{item.title}}</p>
                </li>
            </ul>
        </van-swipe-item>
        </van-swipe>
    </div>
    <!-- 附近商家 -->
    <div class="shop">
        <p><van-icon size="16" name="shop-collect-o" />附近商家</p>
        <ul>
            <li :key="index" v-for="(item,index) in shop">
                <img :src="'http://elm.cangdu.org/img/'+item.image_path" alt="">
                <ol>
                    <li class="li-top">
                        <div>
                            <p>品牌</p>
                        <h2>{{item.name}}</h2>
                        </div>
                        <span>保准票</span>
                    </li>
                    <li class="li-center">
                        <img src="2c7ecd2126c7294ebfbc825b89217dfc.png" alt="">
                    </li>
                </ol>
            </li>
        </ul>
    </div>
    <!-- 脚部 -->
    <ul class="footer">
      <li @click="dian(item.id)" :key="item.id" v-for="item in foot" :class="item.flag?'active':''">
        <van-icon size="26" :name="item.type" />
        <span>{{item.text}}</span>
      </li>
    </ul>
</div>
</template>

<script>
import { Toast } from 'vant'
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
       foot:[
       {text:'外卖',type:'phone-o',id:0,flag:true},
       {text:'搜索',type:'eye-o',id:1,flag:false},
       {text:'订单',type:'orders-o',id:2,flag:false},
       {text:'我的',type:'manager',id:3,flag:false}
     ],
     nav:[],//九宫格
     site:'',
     shop:[],//附近商家
   }
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    //   跳转页面
    tiao(){
        this.$router.push('/user')
    },
      // 点击脚部事件
    dian(i){
      this.foot[i].flag=true
      this.foot.forEach(item=>{
        if(item.id!=i){
          item.flag=false
        }
      })
      if(i==3){
          this.$router.push('/user')
      }
    },
    // 获取当前地点的数据
    async getSite(){
        let {data} = await this.$site.get(`v2/pois/28.234108,113.009931`)
        this.site=data.name
        // 获取九宫格的数据
        let {data:nav} = await this.$http.get(`http://elm.cangdu.org/v2/index_entry?geohash=28.234108,113.009931&group_type=1&flags[]=F
`)
this.nav = nav
// 获取附近商家的数据
let {data:shop} = await this.$http.get(`http://elm.cangdu.org/shopping/restaurants?latitude=28.234108&longitude=113.009931&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`)
    this.shop=shop
    console.log(this.shop)
    },
    // 九宫格样式
    onChange(index) {
    //   Toast('当前 Swipe 索引：' + index);
    },
  },
  mounted(){
      this.getSite()
  }
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss" scoped>
// 顶部
.van-sticky > div {
  width: 100%;
  height: 44px;
  background: #3190e8;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  box-sizing: border-box;
  padding: 10px;
  p{
      font-size: 16px;
      width: 45%;
      overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  }
  .van-icon{
      font-size: 20px;
  }
}
// footer部分
.footer{
  position: fixed;
  width: 100%;
  height: 50px;
  border-top: 1px solid #ccc;
  background: white;
  display: flex;
  align-items: center;
  bottom: 0;
  li{
    width: 25%;
    height: 100%;
   display: flex;
    flex-direction: column;
  align-items: center;
  justify-content: center;
  span{
    font-size: 12px;
  }
  }
}
.active{
  .van-icon{
    color:#3190e8;
  }
}
// 九宫格部分
.center{
    width: 100%;
    height: 220px;
    background-color: white;
    margin-bottom: 10px;
    .van-swipe{
        width: 100%;
        height: 100%;
        .van-swipe-item{
        width: 100%;
        height: 100%;
        ul{
            display: flex;
            flex-wrap: wrap;
            box-sizing: border-box;
            padding-top: 10px;
            li{
                width: 25%;
                display: flex;
                flex-direction: column;
                align-items: center;
                img{
                    width: 40px;
                    height: 40px;
                }
            }
        }
        }
    }
}
// 附近商家部分
.shop{
    width: 100%;
    background: white;
    box-sizing: border-box;
    padding: 10px;
    >p{
        color: #999;
        font-size: 14px;
        text-align: left;
        margin: 0;
        display: flex;
        align-items: center;
        .van-icon{
            margin-right: 5px;
        }
    }
    >ul{
        >li{
            height: 100px;
        }
        li{
            display: flex;
            align-items: center;
            justify-content: space-between;
            // 右边部分
            ol{
                display: flex;
           flex-direction: column;
           flex: 1;
           box-sizing: border-box;
           padding-left:20px ;
        //    最上一层
                .li-top{
                   div{
                        display: flex;
                        width: 86%;
                        height: 20px;
                        align-content: center;
                   }
                    p{
                        font-size: 12px;
                        font-weight: bold;
                        width: 14%;
                        height: 16px;
                        background:  #ffd930;
                        margin: 0 10px 0 0;
                    }
                    h2{
                        font-size: 15px;
                        margin: 0;
                        width: 64%;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        text-align: left;
                    }
                    span{
                        font-size: 12px;
                        color: #999;
                    }
                }

                // 中间部分
    .li-center{
        margin-top: 10px;
        img{
            width: 100%;
        height: 36px;
        }
        
    }
                // 最下一层
            }
        }
        // 图片部分
        img{
            width: 60px;
            height: 60px;
        }
    }
}
</style>
