import { Button } from 'vant';
<template>
<div class="box">
    <!-- 这里是跳转到的城市详情页面 -->
     <!-- 顶部 -->
    <van-sticky>
      <div>
        <router-link to="/pb">〈</router-link>
        <h2>{{city.name}}</h2>
        <router-link to="/pb">切换城市</router-link>
      </div>
    </van-sticky>
    <!-- 输入搜索部分 -->
    <div class="center">
        <input type="text" placeholder="输入校园、商务楼、地址" v-model="text">
        <button @click="search">提交</button>
        <span v-show="show" @click="text=''">❌</span>
    </div>
    <p v-show="!flag">搜索历史</p>
    <ul v-show="flag">
        <li @click="dian(item)" :key="index" v-for="(item,index) in di">
          <p>{{item.name}}</p>
          <span>{{item.address}}</span>
        </li>
    </ul>
    <ul v-show="!flag&&arr.length!=0">
        <li @click="dian(item)" :key="index" v-for="(item,index) in arr">
          <p>{{item.name}}</p>
          <span>{{item.address}}</span>
        </li>
        <li class="del" @click="del"><p>清空所有</p></li>
    </ul>
</div>
</template>

<script>
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
       city:JSON.parse(sessionStorage.getItem('city')),//当前城市
       text:'',
       di:[],//模糊搜索到的城市
       flag:false,//判断搜索到的列表是否出现
      arr:JSON.parse(localStorage.getItem('arr'))||[],
      show:false//红叉的显示
   }
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {
    // 历史记录
    arr:{
      handler(val){
        localStorage.setItem('arr',JSON.stringify(val))
      },deep:true
    },
    // 输入框变化
    text(val){
      if(val!=''){
        this.show=true
      }else{
        this.show=false
      }
    }
  },
  // 组件方法
  methods: {
    // 模糊搜索，提交事件
    search(){
      this.flag=true
        // console.log('hhh')
        this.$http.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.city.id}&keyword=${this.text}`).then((data)=>{
                    this.di = data.data
                    // console.log(this.di)
                  })
    },
    // 每一个列表的点击事件
    dian(i){
      this.arr.unshift(i)
      // console.log(i)
      localStorage.setItem('arr',JSON.stringify(this.arr.slice(0,5)))
      this.$router.push('/site')
    },
    // 全部清空
    del(){
      this.arr=[]
    }
  },
  mounted(){
//     console.log(this.arr)
//  if(this.arr.length>5){
//           this.arr.slice(0,5)
//         }
  }
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss" scoped>
// 顶部
.van-sticky > div {
  width: 100%;
  height: 44px;
  background: #3190e8;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  box-sizing: border-box;
  padding: 10px;
  h2 {
    margin-left: 14%;
  }
  a {
    color: white;
  }
}
// 中间的搜索部分
.center{
    width: 100%;
    height: 120px;
    background: white;
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-sizing:border-box;
    padding: 12px 0;
    position: relative;
    input{
        width: 90%;
        height: 40px;
        border-radius: 5px;
        border:1px solid #ccc;
        text-indent: 20px;
    }
    button{
        width: 92%;
        height: 40px;
        border-radius: 5px;
        background: #3190e8;
  color: white;
  border: none;
    }
    // 红叉
    span{
      position: absolute;
      top: 24px;
      right:30px;
    }
}
// 搜索的列表
ul{
  margin-top: 2px;
  display: flex;
  flex-direction: column;
  li{
    width: 100%;
    height: 80px;
    background: white;
    margin-bottom: 1px;
    display: flex;
  flex-direction: column;
  text-align: left;
  box-sizing:border-box;
  padding: 0 20px;
    // p{

    // }
    span{
      color:#ccc;
      font-size: 14px;
    }
  }
}
// 历史记录
.box>p{
  text-align: left;
  font-size: 14px;
  margin: 0;
  padding: 2px 0;
  text-indent: 10px;
  border-bottom: 2px solid #ddd;
}
.del{
  height: 50px;
  text-align: center;
}
</style>
